.home_page {
    .pic {
        display: none !important;
    }

    .top_bg {
        height: calc(100vh - 0px);
        width: 100%;
        position: relative;

        @media screen and (max-width: 1200px) {
            height: calc(100vh - 0px);
        }

        .botom_mask {
            position: absolute;
            bottom: 0;
            width: 100vw;
            height: 100px;
            z-index: 0;

            @media screen and (max-width: 1200px) {
                background-color: #000000;


            }
        }

        .botom_mask2 {
            position: absolute;
            bottom: 100px;
            width: 100vw;
            height: 300px;
            z-index: 0;
            background: transparent;

            @media screen and (max-width: 1200px) {
                background: linear-gradient(180.00deg, rgba(239, 239, 239, 0), rgb(0, 0, 0), rgb(0, 0, 0), rgb(0, 0, 0) 100%);

            }
        }

        .bg {

            .img_box {
                overflow: hidden;
                position: relative;

                .img_bg {
                    height: calc(100vh - 0px);
                    min-width: 100vw;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    object-fit: cover;

                    @media screen and (max-width: 1200px) {
                        height: calc(100vh - 0px);
                    }

                    .text1 {
                        width: 900px;
                        max-width: 90vw;
                        height: 207.69px;
                        background-repeat: no-repeat;
                        background-size: contain;
                        animation: ball2 2s;

                        @keyframes ball2 {
                            0% {
                                opacity: 0;
                                transform: translateY(-50%);
                            }

                            100% {
                                opacity: 1;
                                transform: translateY(0);
                            }
                        }
                    }
                }

                .text {
                    position: absolute;
                    bottom: 50%;
                    left: 50%;
                    transform: translate(-50%);
                    width: 900px;
                    height: 207.69px;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                }

                .info {
                    position: absolute;
                    bottom: 10%;
                    left: 5%;



                    .time {
                        font-size: 48px;
                        font-weight: 400;
                        letter-spacing: 0px;
                        line-height: 48.28px;
                        color: rgba(255, 255, 255, 1);
                        text-align: left;
                        vertical-align: top;

                        @media screen and (max-width: 1200px) {
                            font-size: 3.5vw;
                            display: none;
                        }
                    }

                    .title {
                        margin-top: 29px;
                        max-width: 1000px;
                        font-size: 80px;
                        font-weight: 700;
                        letter-spacing: 0px;
                        line-height: 88px;
                        color: rgba(255, 255, 255, 1);
                        text-align: left;
                        vertical-align: top;

                        @media screen and (max-width: 1200px) {
                            font-size: 7vw;
                            line-height: 8vw;

                            margin-top: 0px;

                        }
                    }
                }
            }


        }

        .select {
            position: absolute;
            bottom: 10px;
            right: 50%;
            transform: translateX(50%);
            display: flex;
            z-index: 2;
            max-width: 90vw;

            .title_name {
                color: #fff;
            }

            .item {
                overflow: hidden;

                cursor: pointer;
                margin: 0 3vw;
                height: 63.99px;
                display: flex;
                justify-content: center;
                align-items: center;

                // background-color: rgb(184, 210, 255, 1)
                @media screen and (max-width: 1200px) {
                    width: 20vw;
                    font-size: 3vw;
                }
            }

            .activtyitem {

                position: relative;
                opacity: 1;
                color: rgba(42, 130, 228, 1) !important;

                .buttom {
                    width: 100%;
                    height: 2px;
                    background-color: rgba(42, 130, 228, 1);
                    position: absolute;
                    bottom: -10px;
                    animation: ball 1s;

                    @keyframes ball {
                        0% {
                            left: -100%;
                        }

                        100% {
                            left: 0px;
                        }
                    }
                }

            }

            // &::after {
            //     content: '';
            //     width: 100%;
            //     height: 100%;
            //     position: absolute;
            //     background-color: rgb(184, 210, 255, 0.2);
            //     top: 0;
            // }
        }


    }

    .content {
        .event {
            width: 100vw;
            background: linear-gradient(180deg, rgba(42, 130, 228, 0.2) 0%, rgba(42, 130, 228, 0) 100%);
            padding-bottom: 300px;

            @media screen and (max-width: 1200px) {
                padding-bottom: 20vw;

            }

            .title_h1 {
                padding-top: 131px;
                padding-left: 35px;
                font-size: 80px;
                font-weight: 700;
                letter-spacing: 0px;
                line-height: 1.1;
                color: rgba(20, 20, 20, 1);
                text-align: left;
                vertical-align: top;
                padding-bottom: 40px;
                border-bottom: 1px solid #ccc;
                

                @media screen and (max-width: 640px) {
                    font-size: 3rem;
                    padding-top: 100px;
                }
            }

            .infomation {
                width: 100%;
                margin-top: 62px;
                border-left: 1px solid #ccc;
                padding-top: 100px;
                padding-bottom: 40px;
                padding-left: 32px;
                margin-left: 12vw;

                @media screen and (max-width: 1100px) {
                    font-size: 10vw;
                    padding-top: 5vw;
                    margin-top: 5vw;
                    padding: 5vw;

                }

                .title {
                    width: 500px;
                    font-size: 40px;
                    font-weight: 700;
                    letter-spacing: 0px;
                    line-height: 56px;
                    color: rgba(56, 56, 56, 1);
                    text-align: left;
                    vertical-align: top;

                    @media screen and (max-width: 1200px) {
                        font-size: 6vw;
                        line-height: 8vw;
                        padding-top: 5vw;
                        width: 70vw;
                    }
                }

                .info {
                    font-size: 20px;
                    font-weight: 400;
                    letter-spacing: 0px;
                    line-height: 32px;
                    color: rgba(110, 110, 110, 1);
                    text-align: left;
                    vertical-align: top;
                    margin-bottom: 45px;
                    margin-top: 22px;

                    @media screen and (max-width: 1200px) {
                        font-size: 3vw;
                        line-height: 6vw;
                        margin: 2vw auto;
                    }
                }

                span {
                    font-size: 16px;
                    font-weight: 700;
                    position: relative;
                    cursor: pointer;

                    @media screen and (max-width: 1200px) {
                        font-size: 3vw;
                        line-height: 6vw;
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: -10px;
                        left: 0;
                        width: 96px;
                        height: 2px;
                        background-color: #000000;

                        @media screen and (max-width: 1200px) {
                            width: 20vw;

                        }
                    }

                    &::before {
                        content: '';
                        position: absolute;
                        left: 100px;
                        bottom: 5px;
                        width: 7px;
                        height: 7px;
                        border-right: 2px solid #000000;
                        border-bottom: 2px solid #000000;
                        transform: rotate(-45deg);

                        @media screen and (max-width: 1200px) {
                            left: 20vw;
                            bottom: 1.5vw;
                        }
                    }
                }
            }

            .img_info {
                position: relative;

                @media screen and (max-width: 1200px) {
                    margin-top: 10vw;

                }

                .time {
                    position: absolute;
                    top: 208px;
                    left: -45px;
                    width: 90px;
                    height: 90px;
                    background-color: rgba(42, 130, 228, 1);
                    color: #fff;
                    display: flex;
                    flex-direction: column;
                    padding-left: 18px;
                    box-sizing: border-box;
                    justify-content: center;

                    @media screen and (max-width: 640px) {
                        top: -25px;
                        width: 64px;
                        height: 64px;
                        margin-left: 70px;
                    }


                    &>span:last-child {
                        font-size: 20px;
                        font-weight: 700;
                        letter-spacing: 0px;
                        line-height: 27.24px;
                        color: rgba(255, 255, 255, 1);
                        text-align: left;
                        vertical-align: top;

                        @media screen and (max-width: 1200px) {
                            font-size: 1.6vw;


                        }
                    }

                    &>span:first-child {
                        font-size: 14px;
                        font-weight: 700;
                        letter-spacing: 0px;
                        line-height: 19.07px;
                        color: rgba(255, 255, 255, 1);
                        text-align: left;
                        vertical-align: top;

                        @media screen and (max-width: 1200px) {
                            font-size: 2.4vw;


                        }
                    }
                }

                .img_pri {
                    position: relative;
                    background-position: center;
                    margin: 0 auto;
                    max-width: 80vw;
                    width: 960px;
                    background-repeat: no-repeat;
                    background-size: cover;
                    height: 660px;

                    @media screen and (max-width: 1600px) {
                        margin-left: 200px;
                        height: 30vw;
                        top: 10vw;

                    }

                    @media screen and (max-width: 1200px) {
                        margin: 0 auto;
                        height: 50vw;
                    }
                }
            }
        }

        .leading {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 800px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            font-size: 5rem;
            font-weight: 700;
            letter-spacing: 0px;
            line-height: 1.3;
            color: rgba(255, 255, 255, 1);
            text-align: center;
            vertical-align: top;
            @media screen and (max-width: 1100px) {
                font-size: 4rem;
            }
            @media screen and (max-width: 640px) {
                font-size: 2rem;
            }
        }

        .products_item {
            width: 100%;
            min-height: 950px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            position: relative;
            overflow: hidden;
            @media screen and (max-width: 640px) {
            height: 1000px;
            }            
            .act_img{
                opacity: 1 !important;
                right: -20vw !important;

            }
            .title_h1 {
                width: 100%;
                border-bottom: 1px solid #ccc;
                border-top: 1px solid #ccc;
                padding-top: 131px;
                margin-left: 35px;
                padding-bottom: 60px;
                font-size: 80px;
                font-weight: 700;
                letter-spacing: 0px;
                line-height: 1.1;
                color: rgba(20, 20, 20, 1);
                text-align: left;
                vertical-align: top;
                @media screen and (max-width: 640px) {
                font-size: 3rem;
                padding-bottom: 30px;
            }                
            }

            .item_img {
                opacity: 0;
                position: absolute;
                bottom: 0;
                right: -20vw;
                width: 80vw;
                max-width: 2000px;
                max-height: 1000px;
                height: 80vh;
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                transition: all 0.5s;
                 @media screen and (max-width: 640px) {
                 width: 120vw;
                 right: 0;
                 height: 60vh;
            }  
            }

            .infomation {
                height: 500px;
                padding-left: 19px;
                margin-top: 74px;
                border-left: 1px solid #ddd;
                margin-left: 12vw;
                 @media screen and (max-width: 640px) {
                padding-left: 10px;
                margin-left: 25px;
            }                 


                .titleb {
                    font-size: 40px;
                    font-weight: 700;
                    letter-spacing: 0px;
                    line-height: 49.61px;
                    color: rgb(29, 29, 29);
                    text-align: left;
                    vertical-align: top;
                    margin-bottom: 24px;
                    display: flex;
                    align-items: center;
                    position: relative;
                    cursor: pointer;
                @media screen and (max-width: 640px) {
                    font-size: 1.5rem;
                    line-height: 1.2;
            }                     
                    &:hover{
                        color: rgba(42, 130, 228, 1);
                    }
                }

                .title {
                    font-size: 40px;
                    font-weight: 700;
                    letter-spacing: 0px;
                    line-height: 49.61px;
                    color: rgba(42, 130, 228, 1);
                    text-align: left;
                    vertical-align: top;
                    margin-bottom: 14px;
                    display: flex;
                    align-items: center;
                    position: relative;
                     @media screen and (max-width: 640px) {
                    font-size: 1.5rem;
                    line-height: 1.2;
            }                     
                    
                    .svg_right {
                        margin-left: 20px;
                        width: 15px;
                        height: 12.74px;
                        background-repeat: no-repeat;
                        background-size: contain;
                        background-position: center;

                        @media screen and (max-width: 750px) {
                            display: none;
                        }
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        height: 18px;
                        width: 1px;
                        background-color: rgba(42, 130, 228, 1);
                        top: 50%;
                        transform: translateY(-50%);
                        left: -20px;

                        @media screen and (max-width: 750px) {
                            display: none;
                        }
                    }
                }

                .info {
                    margin-bottom: 10px;
                    width: 420px;
                    font-size: 20px;
                    font-weight: 400;
                    letter-spacing: 0px;
                    line-height: 1.2;
                    color: rgba(110, 110, 110, 1);
                    text-align: left;
                    vertical-align: top;
                    overflow: hidden;
                    height: 0;
                    transition: all 0.5s;
                     @media screen and (max-width: 640px) {
                    font-size: 14px;
                    width: 90%;
            }                      
                    &:hover{
                        color: #000;
                    }
                }


                .info2 {
                    font-size: 40px;
                    font-weight: 700;
                    letter-spacing: 0px;
                    line-height: 49.61px;
                    color: rgba(56, 56, 56, 1);
                    text-align: left;
                    vertical-align: top;
                }
            }

            .box {
                height: 500px;
                width: 500px;
            }
        }

        .link {
            .title_h1 {
                width: calc(100% - 70px);
                border-bottom: 1px solid #ccc;
                border-top: 1px solid #ccc;
                padding-top: 131px;
                margin-left: 35px;
                margin-right: 35px;
                padding-bottom: 60px;
                font-size: 80px;
                font-weight: 700;
                letter-spacing: 0px;
                line-height: 1.1;
                color: rgba(20, 20, 20, 1);
                text-align: left;
                vertical-align: top;
                margin-bottom: 67px;
                @media screen and (max-width: 640px) {
                font-size: 3rem;
                padding-bottom: 30px;                         
            }
            }

            .link_img {
                width: 25vw;
                height: 80px;
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                margin: 15% 0;                 
                @media screen and (max-width: 640px) {
                width: 45vw;
                height: 45px;                       
            }
            }
        }
    }
}