.about_news_info {
    overflow: hidden;

    .title {
        background-color: rgb(184, 210, 255, 0.1);
        width: 100%;
        min-height: 180px;
        display: flex;
        justify-content: center;
        padding: 41px;
        align-items: center;
        .title_box{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: start;
            max-width: 700px;
    
        }
        .title_info {
            font-size: 40px;
            font-weight: 700;
            margin-bottom: 37px;

            max-width: 700px;
        }

        .time {
            margin-bottom: 37px;
            width: 84px;
            height: 93px;
            color: #ffffff;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            font-size: 20px;
            font-weight: 400;
            line-height: 32px;
            margin-right: 53px;
            background-color: rgb(0, 61, 166);

        }

        .title_ps {
            color: rgb(142, 142, 142);
            font-size: 16px;
        }
    }

    .info {
        padding: 10px;
        margin: 70px auto;
        max-width: 720px;
        color: rgb(85, 85, 85, 0.8);
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: 0px;
        text-align: justify;
    }

    .img {


        width: 100%;
        height: 588px;
        display: flex;
        justify-content: center;

        img {
            height: 100%;
            max-width: 1200px;
            object-fit: cover;

        }
    }
}