.card_opportunities {
    width: 100%;
    height: 100%;
    background: linear-gradient(180.00deg, rgba(239, 239, 239, 1.00), rgba(236, 241, 249, 0.00) 100%);
    padding: 26px 37px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    position: relative;
    transition: all 0.5s;
    &:hover {
        background: rgb(0, 61, 166);
        color: #fff;
        .title,.title_tag,.name,.type,.info{
            color: #fff !important;
        }
        .read_more{
            &::after {
                content: '';
                position: absolute;
                bottom: -10px;
                left: 0;
                width: 96px;
                height: 1px;
                background-color: #ffffff;
            }
    
            &::before {
                content: '';
                position: absolute;
                left: 90px;
                bottom: 5px;
                width: 7px;
                height: 7px;
                border-right: 2px solid #ffffff;
                border-bottom: 2px solid #ffffff;
                transform: rotate(-45deg);
            }
        }
        .name {
            cursor: pointer;
            &::after {
                content: '';
                background-image: url('../../static/svg/upload2.svg') !important;
            }
        }
    }

    &>span {
        font-size: 14px;
        position: absolute;
        cursor: pointer;
        bottom: 26px;
        right: 37px;

        &::after {
            content: '';
            position: absolute;
            bottom: -10px;
            left: 0;
            width: 96px;
            height: 1px;
            background-color: #000000;
        }

        &::before {
            content: '';
            position: absolute;
            left: 90px;
            bottom: 5px;
            width: 7px;
            height: 7px;
            border-right: 2px solid #000000;
            border-bottom: 2px solid #000000;
            transform: rotate(-45deg);
        }
    }

    .upload_svg {
        position: absolute;
        right: 67px;
        top: 26px;

        .name {
            color: rgb(142, 142, 142);
            font-size: 16px;
            font-weight: 500;
            line-height: 16px;
            letter-spacing: 0px;
            text-align: left;
            position: relative;

            &::after {
                content: '';
                position: absolute;
                right: -30px;
                top: 0;
                width: 24.79px;
                height: 19px;
                background-image: url('../../static/svg/upload.svg');
            }
        }
    }

    .title_tag {
        color: rgb(142, 142, 142);
        font-size: 12px;
        font-weight: 500;
        line-height: 16px;
        letter-spacing: 0px;
        text-align: left;
        margin-bottom: 18px;
    }

    .title {
        color: rgb(0, 0, 0);
        font-size: 28px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0px;
        text-align: left;
    }

    .infomation {
        display: flex;

        .item {
            margin-right: 41px;

            .type {
                margin-bottom: 5px;

                color: rgb(142, 142, 142);
                font-size: 12px;
                font-weight: 500;
                line-height: 16px;
                letter-spacing: 0px;
                text-align: left;
            }

            .info {
                color: rgb(0, 0, 0);
                font-size: 24px;
                font-weight: 400;
                line-height: 20px;
                letter-spacing: 0px;
                text-align: left;
            }
        }
    }
}

.ant-modal-content {
    padding: 0 !important;
    border-radius: 0 !important;
}

.ant-modal-wrap {
    .content {
        padding: 20px 24px;
        max-height:
            60vh;
    }

    .title {
        margin: 26px 0;

        color: rgb(0, 0, 0);
        font-size: 28px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0px;
        text-align: left;
    }

    .infomation {
        display: flex;
        border-bottom: 1px dashed #00000030;
        padding-bottom: 36px;

        .item {
            margin-right: 41px;

            .type {
                margin-bottom: 5px;
                color: rgb(142, 142, 142);
                font-size: 12px;
                font-weight: 500;
                line-height: 16px;
                letter-spacing: 0px;
                text-align: left;
            }

            .info {
                color: rgb(0, 0, 0);
                font-size: 24px;
                font-weight: 400;
                line-height: 20px;
                letter-spacing: 0px;
                text-align: left;
            }
        }
    }

    .info2 {
        overflow-y: auto;
        margin-top: 36px;
        margin-bottom: 186px;

        .info2_title {
            color: rgb(0, 0, 0);
            font-family: Noto Sans SC !important;
            font-size: 14px;
            font-weight: 700;
            line-height: 21px;
            letter-spacing: 0px;
            text-align: left;
        }

        .info2_text {
            margin-top: 19px;
            margin-bottom: 36px;
            opacity: 0.8;
            color: rgb(85, 85, 85);
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            letter-spacing: 0px;
            text-align: justify;
        }
    }

    .name {
        cursor: pointer;
        color: rgb(142, 142, 142);
        font-size: 16px;
        font-weight: 500;
        line-height: 16px;
        letter-spacing: 0px;
        text-align: left;
        position: relative;
        width: 100%;
        background-color: rgb(227, 229, 239);
        height: 64px;
        display: flex;
        justify-content: center;
        align-items: center;

        .svg {
            content: '';
            margin-left: 20px;
            width: 24.79px;
            height: 19px;
            background-image: url('../../static/svg/upload.svg');
        }
    }
}